.searchBar {
	width: 100%;
	height: 15.5%;
	position: fixed;
	margin-top: -1em;
  	background: url("./lunarsurface.png") transparent;
	background-position: bottom;
	background-size: 100% auto;
	background-repeat: no-repeat;
	/* border-bottom: solid white 3px; */
}

.searchBar > * {
	display: inline-block;
}

.appTitle {
	font-size: 3em;
	
	color: white;
	float: left;
	margin-left: 2em;
	margin-top: .5em;
	font-weight: bold;
	-webkit-text-stroke: .5px #212121;
}

.searchField {
	margin-top: 1.2em;
	font-size: 1.7em;
	width: 50%;
}

.searchField > * {
	display: inline;
}
.searchForm {
	font-size: .6em;
	width: 40%;
    padding: .2em .3em;
    box-sizing: border-box;
    resize: none;
    border-bottom: 5px transparent solid;
}

.searchField > form > * {
	margin-right: 1em;
}

.searchField > form > button {
    border-radius: 3px;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0 10px;
    line-height: 32px;
    height: 32px;
    text-align: center;
    font-size: 12px;
    font-weight: 400;
    letter-spacing: .1em;
    outline: none;
    text-decoration: none;
    cursor: pointer;
    font-family: "Open Sans", sans-serif;
}

.searchField > form > button:hover {
	background-color: black;
	color: white;
}

.searchField > form > button:hover:active {
	border: #404040 solid 1px;
	background-color: #404040;
}