body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
}

body {
  background-color: #212121;
}

.hideBackground {
  pointer-events: none;
  opacity: .45;
  transition: opacity 1.2s ease-out;
  -moz-transition: opacity 1.2s ease-out;
  -webkit-transition: opacity 1.2s ease-out;
}

.showBackground {
  opacity: 1;
  transition: opacity .6s ease-out;
  -moz-transition: opacity .6s ease-out;
  -webkit-transition: opacity .6s ease-out;
}

@media screen and (min-width: 1824px) {
	.App {
  		font-size: 1.65em;
  	}
}
.searchResults {
  padding-top: 6em;
}
li  {
	display: inline-block;
	height: 100px;
	margin: 1em;
}

li img {
	height: 100%;	
	padding: 1px;
	background-color: #A8A8A8;
	border: 1px #212121 solid;
}

li img:hover {
	border: 1px white solid;
}

li img:hover:active {
	border: 2px white solid;
	margin: -1px;
}

.favoriteHandler:hover:active {
	font-weight: bold;
}
.noResults  {
	color: white;
	font-size: 2.5em;
}
.searchBar {
	width: 100%;
	height: 15.5%;
	position: fixed;
	margin-top: -1em;
  	background: url(/nasa-search/static/media/lunarsurface.a262bd37.png) transparent;
	background-position: bottom;
	background-size: 100% auto;
	background-repeat: no-repeat;
	/* border-bottom: solid white 3px; */
}

.searchBar > * {
	display: inline-block;
}

.appTitle {
	font-size: 3em;
	
	color: white;
	float: left;
	margin-left: 2em;
	margin-top: .5em;
	font-weight: bold;
	-webkit-text-stroke: .5px #212121;
}

.searchField {
	margin-top: 1.2em;
	font-size: 1.7em;
	width: 50%;
}

.searchField > * {
	display: inline;
}
.searchForm {
	font-size: .6em;
	width: 40%;
    padding: .2em .3em;
    box-sizing: border-box;
    resize: none;
    border-bottom: 5px transparent solid;
}

.searchField > form > * {
	margin-right: 1em;
}

.searchField > form > button {
    border-radius: 3px;
    box-sizing: border-box;
    padding: 0 10px;
    line-height: 32px;
    height: 32px;
    text-align: center;
    font-size: 12px;
    font-weight: 400;
    letter-spacing: .1em;
    outline: none;
    text-decoration: none;
    cursor: pointer;
    font-family: "Open Sans", sans-serif;
}

.searchField > form > button:hover {
	background-color: black;
	color: white;
}

.searchField > form > button:hover:active {
	border: #404040 solid 1px;
	background-color: #404040;
}
.mm-popup {
    display: none;
}

.popupImage {
    max-height: 68vh;
    max-width: 90vw;
    padding: 1px;
    border: 1px black gray;
    background-color: lightgray;
}

.mm-popup--visible {
    display: block;
}


.mm-popup__btn {
    border-radius: 3px;
    box-sizing: border-box;
    padding: 0 10px;
    margin: 0;
    line-height: 32px;
    height: 32px;
    border: 1px solid #666;
    text-align: center;
    display: inline-block;
    font-size: 12px;
    font-weight: 400;
    color: #333;
    background: transparent;
    outline: none;
    text-decoration: none;
    cursor: pointer;
    font-family: "Open Sans", sans-serif;
}

.mm-popup__btn:hover {
    background-color: black;
    color: white;
}

.mm-popup__btn:hover:active {
    border: black solid 1px;
    background-color: #404040;
}

.mm-popup__box {
    position: fixed;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: #fff;
    box-shadow: 0px 5px 20px 0px rgba(126,137,140,0.20);
    border-radius: 5px;
    border: 1px solid #B8C8CC;
    overflow: hidden;
    z-index: 1001;
}

.mm-popup__box__header {
    padding: 13px 20px 11px;
    background: #2e2e2e;
    color: white;
    letter-spacing: .02em;
    word-spacing: .05em;
}

.mm-popup__box__header__title {
    margin: 0;
    font-size: 16px;
    text-align: left;
    font-weight: 600;
}

.mm-popup__box__body {
    padding: 20px;
    line-height: 1.4;
    font-size: 14px;
    color: #454B4D;
    background: #fff;
    position: relative;
    z-index: 2;
}

.mm-popup__box__body p {
    margin: 0 0 5px;
}

.mm-popup__box__footer {
    overflow: hidden;
    padding: 40px 20px 20px;
    margin-top: -2.5em;
}

.mm-popup__box__footer__right-space {
    float: right;
}

.mm-popup__box__footer__right-space .mm-popup__btn {
    margin-right: 5px;
}

.mm-popup__box__footer__left-space {
    float: right;
}

.mm-popup__box__footer__left-space .mm-popup__btn {
    margin-right: 13px;
}

.popupDesc {
    margin-top: .7em;
}
