li  {
	display: inline-block;
	height: 100px;
	margin: 1em;
}

li img {
	height: 100%;	
	padding: 1px;
	background-color: #A8A8A8;
	border: 1px #212121 solid;
}

li img:hover {
	border: 1px white solid;
}

li img:hover:active {
	border: 2px white solid;
	margin: -1px;
}

.favoriteHandler:hover:active {
	font-weight: bold;
}